html {
  --accent-color: #00acdc;
}

body {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo_img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_list {
  align-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_link {
  text-align: center;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: var(--accent-color);
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav_link:hover {
  color: var(--accent-color);
}
